<template>
    <div class="row">
        <CustomeHeaderAlert :message="headertext"/>
        <div class="col-12">
            <div class="card">
                <div class="card-body">
                    <div class="row mb-3">
                        <div class="col-4">
                            <h4>List of groups</h4>
                        </div>
                        <div class="col-8">
                            <div class="text-lg-right mt-3 mt-lg-0">
                                <a
                                        class="btn text-white btn-danger"
                                        href="javascript: void(0);"
                                        @click="showmodal = true">
                                    <i class="mdi mdi-plus-circle mr-1"></i> Create group
                                </a>
                            </div>
                        </div>
                    </div>
                    <div class="row mb-2">
                        <div class="col-sm-12 col-md-6">
                            <div id="tickets-table_length" class="dataTables_length">
                                <label class="d-inline-flex align-items-center">
                                    Display&nbsp;
                                    <b-form-select
                                            v-model="perPage"
                                            size="sm"
                                            :options="pageOptions"
                                    ></b-form-select
                                    >&nbsp;
                                </label>
                            </div>
                        </div>
                        <!-- Search -->
                        <div class="col-sm-12 col-md-6">
                            <div
                                    id="tickets-table_filter"
                                    class="dataTables_filter text-md-right"
                            >
                                <label class="d-inline-flex align-items-center">
                                    Search:
                                    <b-form-input
                                            v-model="filter"
                                            type="search"
                                            placeholder="Search..."
                                            class="form-control form-control-sm ml-2"
                                    ></b-form-input>
                                </label>
                            </div>
                        </div>
                        <!-- End search -->
                    </div>
                    <b-table
                            table-class="table table-centered w-100"
                            thead-tr-class="bg-light"
                            :items="tableData"
                            :fields="fields"
                            responsive="sm"
                            :per-page="perPage"
                            :current-page="currentPage"
                            :sort-by.sync="sortBy"
                            :sort-desc.sync="sortDesc"
                            :filter="filter"
                            :filter-included-fields="filterOn"
                            @filtered="onFiltered"
                    >
                        <template v-slot:cell(id)="data">
                            <b> {{ data.item.id }}</b>
                        </template>
                        <template v-slot:cell(subject)="data">
                            {{ data.item.subject }}
                        </template>

                        <template v-slot:cell(noofmember)="data">
                            {{ data.item.noofmember }}
                        </template>

                        <template v-slot:cell(status)="data">
                      <span
                        class="badge"
                        :class="{
                                'text-primary': data.item.status === 'Private',
                                'text-success': data.item.status === 'Public',
                              }"
                >
                  {{ data.item.status }}</span
                >
                        </template>
                        <template v-slot:cell()="data">
                            <a href="javascript: void(0);" class="text-dark">
<!--                                <img -->
<!--                                        -->
<!--                                        alt="contact-img"-->
<!--                                        title="contact-img"-->
<!--                                        class="avatar-sm rounded-circle img-thumbnail"-->
<!--                                /> -->
                                <span class="avatar-sm rounded-circle img-thumbnail">{{data.item.name.split(" ").map((n)=>n[0]).join("")}}</span>
                                <span class="ml-2">{{ data.item.name }}</span>
                            </a>
                        </template>
                        <template v-slot:cell(action)="data">
                            <a @click="GroupDetails(data.item)" class="text-primary" style="cursor: pointer;"> Proceed</a>
                        </template>
                    </b-table>
                    <div class="row">
                        <div class="col">
                            <div class="dataTables_paginate paging_simple_numbers float-right">
                                <ul class="pagination pagination-rounded">
                                    <!-- pagination -->
                                    <b-pagination
                                            v-model="currentPage"
                                            :total-rows="rows"
                                            :per-page="perPage"
                                    ></b-pagination>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <b-modal
                id="modal-1"
                v-model="showmodal"
                title="Create group"
                header-close-variant="light"
                title-class="font-18"
                hide-footer>
            <form @submit.prevent="CreateGroup">
                <div class="form-group">
                    <label for="name">Name</label>
                    <input
                            id="name"
                            v-model="addgroup.name"
                            type="text"
                            class="form-control"
                            placeholder="Enter name"
                            name="name"
                            :class="{ 'is-invalid': vErrors.has('name') }"
                    />
                    <span v-show="vErrors.has('name')" class="help text-danger">{{ vErrors.first('name') }}</span>
                </div>
                <div class="form-group">
                    <label> Group type</label>
                    <select name="status" class="custom-select" v-model="addgroup.type">
                        <option selected>Select status</option>
                        <option value="public">Public</option>
                        <option value="private">Private</option>
                    </select>
                </div>
                <div class="form-group">
                    <label for="description">Descriptions</label>
                    <textarea v-model="addgroup.description" v-validate="'required'" id="description" class="form-control"  name="description" :maxlength="400" rows="3" placeholder="Group description">
                            </textarea>
                    <span v-show="vErrors.has('description')" class="help text-danger">{{ vErrors.first('description') }}</span>

                </div>

                <div class="text-right">
                    <button type="submit" class="btn btn-success">Save</button>
                    <b-button class="ml-1" variant="danger" @click="hideModal"
                    >Cancel</b-button
                    >
                </div>
            </form>
        </b-modal>
    </div>
</template>

<script>
    import {widgetData, tableData} from "./listgroup";
    import CustomeHeaderAlert from '../../components/shared/CustomHeaderAlert'

    export default {
       data() {
           return {
               showmodal: false,
               headertext :'Active running private and public groups',
            addgroup : {
                 name:'',
                type:'',
                description:''
            },
               tableData: tableData,
               widgetData: widgetData,
               totalRows: 1,
               currentPage: 1,
               perPage: 10,
               pageOptions: [10, 25, 50, 100],
               filter: null,
               filterOn: [],
               sortBy: "age",
               sortDesc: false,
               fields: [
                   {
                       key: "id",
                       label: "ID",
                       sortable: true,
                   },
                   {
                       label: "Group name",
                       key: "name",
                       sortable: true,
                   },
                   {
                       label: "Description",
                       key: "subject",
                       sortable: true,
                   },
                   {
                       label: "Group type",
                       key: "status",
                       sortable: true,
                   },
                   {
                       label :"Number of members",
                       key: "noofmember",
                       sortable: true,
                   },
                   "action",
               ],
           }
       },
        components : {
            CustomeHeaderAlert
        },
        methods: {
            CreateGroup() {

            },
            onFiltered(filteredItems) {
                // Trigger pagination to update the number of buttons/pages due to filtering
                this.totalRows = filteredItems.length;
                this.currentPage = 1;
            },
            hideModal(e) {
                console.log(e)
                this.showmodal = false;
            },
            GroupDetails(data) {
                   console.log("d", data)

                 this.$store.dispatch('SetGroupName', {
                     groupname : data.name
                 })
                this.$router.push('/groupcontribution/business/groupdetails').catch(() => { })
            }
        },
        created() {
            this.$store.dispatch('setAppHeader', {
                title: "List of groups",
                items: [
                    {
                        text: "Dashboard",
                        href: "#",
                    },
                    {
                        text: "List of groups",
                        href: "#",
                    },
                ],
            })
        },
        computed:{
            rows() {
                return this.tableData.length;
            },
        },
        mounted() {
            // Set the initial number of items
            this.totalRows = this.tableData.length;
        },
    }
</script>

<style scoped>

</style>